<template>
  <div id="app" >
    <NavBar/>
    <router-view :products="this.products" />
  </div>
</template>
<script>
import axios from 'axios'
import NavBar from './components/NavBar.vue'
export default {
  name: 'App',
  components: {
    NavBar,
  },
  data (){
    return{
        products: [],
    }
  },
  async created(){
    const resultProduct = await axios.get("/api/products");
    this.products = resultProduct.data
  }
}
</script>

