<template>
  <div id="page-wrap" v-if="product">
    <div id="img-wrap">
      <img v-bind:src="product.imageUrl" />
    </div>
    <div id="product-details">
      <h1>{{ product.name }}</h1>
      <h3 id="price">${{ product.price }}</h3>
      <p>Average rating: {{ product.averageRating }}</p>
      <button
        id="add-to-cart"
        v-if="!itemIsInCart && !showSuccessMessage"
        v-on:click="addToCart"
      >Add to Cart</button>
      <button
        id="add-to-cart"
        class="green-button"
        v-if="!itemIsInCart && showSuccessMessage"
      >Successfully added item to cart!</button>
      <button
        id="add-to-cart"
        class="grey-button"
        v-if="itemIsInCart"
      >Item is already in cart</button>
      <h4>Description</h4>
      <p>{{ product.description }}</p>
    </div>
  </div>
  <NotFoundPage v-else/>
</template>
<script>
import axios from 'axios'
import NotFoundPage from '../views/NotFoundPage.vue'
export default {
    name: 'ProductsPage',
    props: ['products'],
    components: {
      NotFoundPage,
    },
    computed: {
      itemIsInCart() {
        return this.cartItems.some(item => item.id === this.product.id.replace(":",""));
      }
    },
    data() {
        return {
            cartItems: [],
            showSuccessMessage: false,
            product : this.products.find((p) => p.id === this.$route.params.id.replace(":",""))
        }
    },
    methods: {
      async addToCart() {
        await axios.post('/api/users/123/cart', {
          productId: this.$route.params.id.replace(":",""),
        });
        this.showSuccessMessage = true;
        setTimeout(() => {
          this.$router.push('/products');
        }, 1500);
      },
    },
    async created() {
    //   const { data: product } = await axios.get(`/api/products/${this.$route.params.id}`);
    //   this.product = product;

      const { data: cartItems } = await axios.get('/api/users/123/cart');
      this.cartItems = cartItems;
    }
}
    // computed(){
    //     // const result = await axios.get("/api/products");
    //      this.product = this.products.find((p) => p.id === this.$route.params.id.replace(":",""))
    //     console.log("details", this.props);
    // }

</script>

